<template>
  <v-app id="inspire">
    <v-main>
      <v-container fluid fill-height>
        <v-layout align-center justify-center dark>
          <v-flex xs12 sm8 md4>
            <v-card dark class="elevation-12 form-login">
              <v-toolbar dark>
                <v-toolbar-title>AniPlace — Cadastro</v-toolbar-title>
              </v-toolbar>
              <v-card-text>
                <v-form @submit.prevent="cadastrar()">
                  <v-text-field
                      prepend-icon="mdi-account"
                      v-model="nome"
                      label="Nome"
                      type="text"
                      v-on:keyup.enter="cadastrar()"
                  ></v-text-field>
                  <v-text-field
                      prepend-icon="mdi-email"
                      v-model="email"
                      label="E-Mail"
                      type="email"
                      v-on:keyup.enter="cadastrar()"
                  ></v-text-field>
                  <v-text-field
                      prepend-icon="mdi-at"
                      v-model="usuario"
                      label="Usuário"
                      type="text"
                      v-on:keyup.enter="cadastrar()"
                  ></v-text-field>
                  <v-text-field
                      id="password"
                      prepend-icon="mdi-lock"
                      v-model="senha"
                      label="Senha"
                      type="password"
                      v-on:keyup.enter="cadastrar()"
                  ></v-text-field>
                  <v-text-field
                      id="passwordRepeat"
                      prepend-icon="mdi-lock-reset"
                      v-model="senhaRepetir"
                      label="Repita sua Senha"
                      type="password"
                      v-on:keyup.enter="cadastrar()"
                  ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <p class="login-err" v-show="this.$store.state.auth.loginErr">
                  <v-icon>{{this.$store.state.auth.cadastroIcon ? "mdi-alert-circle" : "mdi-account-check"}}</v-icon>
                  <span>{{this.$store.state.auth.cadastroMsg}}</span>
                </p>
                <v-spacer></v-spacer>
                <v-btn :loading="this.$store.state.auth.loading" dark @click="cadastrar()">Cadastrar <v-icon>mdi-account-plus</v-icon></v-btn>
              </v-card-actions>
              <v-card-text>
                <p class="noaccount">Já possui uma conta? <span @click="nav()">Faça Login</span> agora mesmo.</p>
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import {mapActions} from "vuex";

export default {
  name: "CadastroPage",
  data: () => ({
    nome: "",
    email: "",
    usuario: "",
    senha: "",
    senhaRepetir: ""
  }),
  methods: {
    ...mapActions('auth', ['ActionKillSession']),
    ...mapActions('auth', ['ActionCadastro']),
    cadastrar(){
      this.ActionCadastro({
        nome: this.nome,
        email: this.email,
        nomeusuario: this.usuario,
        senha: this.senha,
        senharepetida: this.senhaRepetir
      });
      this.resetForm();
    },
    nav(){
      this.ActionKillSession();
      this.$router.push('/login');
    },
    resetForm(){
      this.nome = "";
      this.email = "";
      this.usuario = "";
      this.senha = "";
      this.senhaRepetir = "";
    }
  }
}
</script>

<style scoped>
#inspire {
  background-image: url(https://i.imgur.com/zBVQoFx.jpeg);
  background-attachment: fixed;
  background-size: cover;
}

.form-login {
  opacity: 65%;
}

.login-err {
  color: #FF0000;
  margin-bottom: 0;
}

.login-err span {
  margin-left: 4px;
}

.noaccount span {
  cursor: pointer;
  transition: 300ms;
  color: #FFFFFF;
}

.noaccount span:hover {
  color: #FFFFFF69;
}
</style>